import parseMagpowerSTA from "./parser/magpower/sta";
import parseM4STA from "./parser/m4/sta";
import parseMagpowerIDN from "./parser/magpower/idn";
import parsePVParameter from "./parser/pv";

interface PVProtocolCatalog {
    request?: string | RegExp,
    validator?: (input: string) => boolean
    products: {
        id: string,
        length?: number,
        validator?: (input: string) => boolean
    }[]
}

const PVProtocolCatalog = [
    {
        "request": /[0-9]{10}=\?[0-9]{1,3}/,
        "products": [{
            id: "PVProtocol",
            parser: parsePVParameter
        }]
    },
    {
        "request": "IDN",
        "products": [{
            id: "MAGPOWER",
            parser: parseMagpowerIDN
        }]
    },
    {
        "request": "STA",
        "products": [
            {
                id: "MAGPOWER",
                length: 77,
                validator: (input: string) => {
                    return input.includes(",")
                },
                parser: parseMagpowerSTA
            },
            {
                id: "M4",
                length: 73,
                validator: (input: string) => {
                    return input.includes(",")
                },
                parser: parseM4STA
            },
            {
                id: "M6",
                length: 73,
                validator: (input: string) => {
                    return !input.includes(",")
                },
                parser: parseM4STA
            }
        ]
    }
    ]

export default PVProtocolCatalog;
