import {PfeifferDataTypeID, PVProtocolOptionID } from "../../types";
import SerialLinkDataParser from "../SerialLinkDataParser";

const MAGPOWER_IDN_CONFIG = [
    {
        id: "SOFTWARE_VERSION",
        name: "Software Version",
        command: "IDN",
        description: "Vx.zz - The software Version (x), the software edition (zz)",
        startPosition: 5,
        dataType: PfeifferDataTypeID.string,
        access: "r"
    }
]

export default function parseMagpowerIDN(input: string) {

    return SerialLinkDataParser.reduceParametersForResponse(MAGPOWER_IDN_CONFIG, input)

}
